.card{
    position: relative;
}

.card img{
    width:100%;
    display:block;
    border: 1px solid white;
    border-radius:6px;
    /* margin-right:1px; */
}

.card .front{
    transform:rotateY(90deg);
    transition:all ease-in 0.2s;
    position:absolute;
}

.flipped .front{
    transform:rotateY(0deg);
    transition-delay: 0.2s;
}

.card .back{
    transition:all ease-in 0.2s;
    transition-delay: 0.2s;
}

.flipped .back{
    transform: roateY(90deg);
    transition-delay: 0s;
}